type SubBarProps = {
    display?: "search-bar" | "filter-bar" | "dates-bar";
}

const SubBar = (props: SubBarProps) => {
    console.log(props)
    return (
        <div>
            {
                props.display === 'filter-bar' ? (
                    <FilterSubBar />
                ) : props.display === 'dates-bar' ? (
                    <DatesSubBar />
                ) : props.display === 'search-bar' ? (
                    <SearchSubBar />
                ) : null
            }
        </div>
    )
};

const DatesSubBar = () => {
    const [value, setValue] = React.useState<Date | null>(new Date());

    const meetDateCriteria = (date: Date) => {
        if (date >= new Date(2022, 2, 28))
            return false

        return true
    }


    return (
        <Container

            maxWidth={"sm"}
        >
            <LocalizationProvider dateAdapter={AdapterDateFns}>
                <StaticDatePicker
                    shouldDisableDate={meetDateCriteria}
                    displayStaticWrapperAs="mobile"
                    openTo="day"
                    value={value}
                    onChange={(newValue: Date | null) => {
                        setValue(newValue);
                    }}

                    renderInput={(params) => <TextField {...params} />}

                />
            </LocalizationProvider>

        </Container >
    )
}

